<template>
<div class="wrapper" ref="wrapper">
      <div class="s-content">
        <div class="area">
            <p class="title"></p>
            <div class="content">
                <div class="btn">
                    <!-- vuex -->
                    {{this.$store.state.city}}
                </div>
            </div>
        </div>

        <div class="area">
            <p class="title"></p>
            <div class="content">
                <div class="btn" v-for="(item,index) of hotCities" :key="index">
                    <div @click="handleclick(item.name)"> {{item.name}}</div>
                  
                </div>
            </div>
        </div>
        <div class="area" v-for="(item,index) in cities" :key="item.id" :ref="index">
            <p class="title">{{index}}</p>
            <div class="list-city" @click="handleclick(index.name)" v-for="(index,inx) of item" :key="inx">{{index.name}}</div>
        </div>
    </div>
</div>
  
</template>
<script>
import BScroll from "better-scroll";
export default {
    name:"CityList",
    props:['hotCities','cities'],
    methods:{
      handleclick(city){
           this.$store.commit("changeCity",city);
           this.$router.push("/");
      }
  },
     mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {});
    });
  },
//wordvuexletter
  computed: {
      //
      letter(){
          return this.$store.state.letter
      }
  },
  watch:{
      //html :ref="index"
      //:ref="index"for
      letter(){
          //console.log(1)
        //   console.log(this.$refs)
          //
          const element = this.$refs[this.letter][0];
          this.scroll.scrollToElement(element);
      }
  },
  
}
</script>
<style lang="scss" scoped>
.wrapper {
  width: 100%;
  height:1000px;
  overflow: hidden;
  position: relative;
  margin-top: 176px;
}

.title{
    padding-left:20px;
    line-height: 60px;
    background: #eee;
}
.btn{
    width:200px;
    height:54px;
    border-radius: 10px;
    border:1px solid #ccc;
    text-align: center;
    line-height: 54px;
}
.content{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    padding:20px;
    grid-row-gap: 20px;
}
.list-city{
    padding-left:20px;
    line-height: 70px;
    border-bottom: 1px solid #ccc;
}
</style>
