import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'
import mutations from './mutations'

Vue.use(Vuex)

export default new Vuex.Store({
  //data
  state,
  mutations,
  //1 mutations
  actions: {
    changeLetter(ctx,letter){
      // console.log(letter);
      // commit
      ctx.commit("changeLetter",letter)
    }
  }
})
